.container {
  margin-top: 50px;
  color: #fff;
}
.container h2 {
  line-height: 140%;
  font-size: 40px;
}
.black {
  background: #000;
  color: #c7bdbd;
  padding: 0 20px 0 20px;
  display: inline-block;
  margin-bottom: 10px;
}
#flipper {
  background: #1099EE;
  padding: 5px 20px 5px 20px;
  color: #000;
}
.facts {
  font-size: 40px;
  margin: 76px auto;
  width: 90%;
  font-family: arial;
  margin-top: 70px;
  margin-bottom: 37px;
  border-top: 1px solid #ccc;
  border-bottom: 1px solid #ccc;
  font-style: italic;
  font-family: georgia;
  padding-top: 10px;
  padding-bottom: 10px;
}
.logo {
  margin-bottom: 20px;
}
.btn-blue {
  background: #1099EE;
  font-size: 24px;
}
.btn-blue:hover {
  color: #000;
}
.select-style {
  border: 1px solid #ccc;
  width: 120px;
  border-radius: 3px;
  overflow: hidden;
  color: #000;
  height: 30px;
  width: 185px;
  margin-top: 10px;
}
.container .QuestionBox {
  margin-top: 14% !important;
}
[role=button] {
  display: inline-block;
  float: left;
  margin-right: 20px;
}
