body {
    /* overflow: hidden; */
    background: transparent !important;
}


body.lock-scroll {
    overflow: hidden;
}

body.lock-scroll .mt-200 {
  background: transparent;
  text-align: center;
}
.mt-200 {
  margin-top: 165px;
}

.tooltip {
    opacity: 1;
}
.slider.slider-horizontal .slider-track {
    height: 10px;
    width: 100%;
    margin-top: -5px;
    top: 50%;
    left: 0;
    height: 40px;
    border-radius: initial;
}
.slider-handle {
    position: absolute;
    top: -23px;
    border-radius: 0 !important;
    width: 10px;
    height: 68px;
}
.tooltip-max.bottom, .tooltip.tooltip-min.top {
    display: none;
}
.slider.slider-horizontal {
    width: 87%;
    height: 20px;
}
.QuestionBox {
    height: 100vh;
}
.Question {
    min-height: 250px;
    color: #fff;
    padding: 20px;
    padding-top: 0;
}
.slider-container {
  min-height: 120px;
  position: relative;
  max-width: 900px;
  margin: 0 auto;
}
.minValue {
  position: absolute;
  left: -4px;
  bottom: 23px;
  color: #000;
  background: #eee;
  padding: 13px;
  padding: 10px 15px 10px 15px;
}
.maxValue {
  position: absolute;
  right: -13px;
  bottom: 23px;
  color: #000;
  background: #eee;
  padding: 13px;
  padding: 10px 15px 10px 15px;
}

.btn-warning {
    color: #212529;
    background-color: #ffc107;
    border-color: #ffc107;
    margin-top: 20px;
    color: #fff;
}
.slider-selection {
    background: #ffc107;
}
.QuestionBox input[type='submit'] {
    margin-top: 50px;
    border-radius: 0;
}
.correct-answer {
    color: #fff;
    margin-top: 30px;
    font-weight: 100;
}
body {
